.App {
  text-align: center;

}
.Input{
  height: 100%;
  width: 100%;
  position: fixed;
  background-repeat: no-repeat;
  background-size: 100%
}
.Form{
  margin: 18vh 0;
}
.temp{
  margin: 0 auto;
  font-size: 10vh;
}
.Input-class{
  position: relative;
  right: 100px;
  font-size: 25px;
  margin:0 50px;
  background-color: rgba(0, 0, 0, 0.44);
  border:0;
  color: white;
  height: 30px;
  width: 200px;
}
.img{
  position: fixed;
  z-index: 9999;
  width: 71px;
  height: 71px;
  background-size: 71px;
  left: 50%;
  top: 48%;
}
.img-div {
  position: fixed;
  margin: 0 auto;
  background-size:100rem;
  width: 100px;
  height: 100px;
  z-index: 999;

}
.Favorites{
  display: flex;
}
.Favorites_div{
  margin:0 auto;
  background-color: rgba(0, 0, 0, 0.64);
  color: white;
  width: 200px;
  text-align: center;
  font-size: 20px;
  border-radius:100% 
}
.ThisWeather{
  display: flex;
  text-align: right;
}
.Clock{
  margin: 0 0 0 auto;
  padding:50px 170px 0;
  color: white;
}
.Wather{
  background-image: url(/static/media/city.cfc5b283.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  position: fixed;
  height:100%;
  width: 100%;
}
.Wath{
  background-color:rgba(0, 0, 0, 0.44);
  text-align: center;
  background-color:rgba(0, 0, 0, 0.44);
  color: white;
  margin: 100px 100px;
  display: flex;

}
.city{
  padding:50px 170px 0;
}
.wather{
  margin: 0 100px;
}
.Input-clas{
  font-size: 25px;
  margin:15px 50px;
  background-color: rgba(0, 0, 0, 0.44);
  border:0;
  color: white;
  height: 30px;
  width: 200px;
}
.btn-in{
  margin: 100px;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  margin: 0 0 0 auto;
}
.btn{
  margin: 100px;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  margin: 0 0 0 auto;
  background-color:rgba(167, 114, 35, 0.74);
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@font-face {
  font-family: 'icomoon';
  src:  url(/static/media/icomoon.741fc918.eot);
  src:  url(/static/media/icomoon.741fc918.eot#iefix) format('embedded-opentype'),
  url(/static/media/icomoon.b875f6a9.ttf) format('truetype'),
  url(/static/media/icomoon.fd0e6fcd.woff) format('woff'),
  url(/static/media/icomoon.c826234b.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-star-o:before {
  content: "\F006";
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

